
















































import ut from '../../utils/index.js';
import Adress from '../../components/common/adress.vue'
import precautions from '../../components/common/precautions.vue'
import {mapState} from 'vuex'
export default {
	data() {
		return {
			static: '',
			serverinf: {},
			date: '',
			time: '',
			y: "",
			m: '',
			d: '',
			remark:'',
			start:ut.date(),
			end:ut.enddate()
		}
	},
	computed: {
		...mapState(['adress'])
	},
	components:{
		Adress,
		precautions
	},
	onLoad() {
		this.static = ut.static;
		this.serverinf = wx.getStorageSync('serverinf');
		console.log(JSON.stringify(this.serverinf))
		wx.setStorageSync('serverinf', '');
	},
	methods: {
		go_mine_adress() {
			wx.navigateTo({
				url: '../mine/adress?src=pay'
			})
		},
		req_unifiedOrder() {
			if (!this.adress) {
				ut.totast('请选择地址');
				return;
			}
			if (!this.date) {
				ut.totast('请选择日期');
				return;
			}
			if (!this.time) {
				ut.totast('请选择时间');
				return;
			}
			if(!ut.timespace(this.date+' '+this.time)){
				ut.totast('请选择两小时后');
				return
			}
			ut.request({
				data: {
					"addressId": this.adress.id,
					"makeTime": this.date +' '+ this.time,
					"remark": this.remark,
					"serviceId": this.serverinf.id
				},
				url: "service/order/unifiedOrder",
				c: true
			}).then(data => {
				wx.requestPayment({
					'timeStamp': data.timeStamp,
					'nonceStr': data.nonceStr,
					'package': data.package,
					'paySign': data.paySign,
					'signType': 'HMAC-SHA256',
					success: function(res) {
						
					},
					'fail': function(res) {
						console.log(res)
					},
					'complete': function(res) {
						wx.redirectTo({
							url: '../order/checkOrder?type=1'
						})
					}
				});
			})
		},
		timeChange: function(e) {
			this.time = e.detail.value;
		},
		dateChange: function(e) {
			this.date = e.detail.value;
			const date = this.date.split('-');
			this.y = date[0];
			this.m = date[1];
			this.d = date[2];
		}
	}
}
